import './read-more-button.scss'

export const ReadMoreButton = ({
  link = '',
  readMoreHandler = '',
  buttonText = ' ',
}) => {
  if (link !== '') {
    return (
      <a href={link} className='read-more'>
        <span> </span>
        <img src='images/arrow.svg' alt='' />
      </a>
    )
  } else {
    return (
      <button onClick={readMoreHandler} className='read-more'>
        <span>{buttonText}</span>
        <img src='images/arrow.svg' alt='' />
      </button>
    )
  }
}
